.stories {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin: 5px;
}

.profilePic {
    height: 150px;
    width: 150px;
    border-radius: 50%;
    overflow-x: scroll;
}

@media (max-width: 600px) {
    .profilePic {
        height: 100px;
        width: 100px;
    }
}